a {
    color: var(--c2);
}

* {
    margin: 0;
    padding: 0;
}

.devs {
    width: 100%;
    height: max-content;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.devs h1 {
    margin-top: 100px;
}

.devs-pages {
    width: 100%;
    height: max-content;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    background-color: var(--c1);
}

.card {
    width: 350px;
    height: 200px;
    border-radius: 10px;
    transition: 0.3s;
    margin: 40px;
}

.card:hover {
    transform: scale(1.1);
    transition: 0.3s;
}

.img img {
    width: 350px;
    height: 200px;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    border-radius: 10px;
}

.card .card-title {
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: absolute;
    width: max-content;
    margin-top: -37px;
    background-color: var(--c4);
    font-family: 'Noto Sans', sans-serif;
    font-size: 1.2em;
    margin-left: 0.2px;
    padding: 0 7px 0 7px;
    border-radius: 0 0 0 2px;
    font-weight: 600;
}

.card-title i {
    color: var(--c2);
    margin-left: 6px;
    font-size: 15px;
}

@media(max-width: 800px) {
    .devs h1 {
        margin-top: 600px;
    }
    .devs-pages {
        background-color: var(--c1);
    }
}