@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;500;600&display=swap');
:root {
    --c1: #363636;
    --c2: #ffffff;
    --c3: #FF6347;
    --c4: #FF8C00;
    --c5: #FF7F50;
}

ul,
a {
    text-decoration: none;
    list-style: none;
}

.section {
    display: flex;
    width: 100%;
    height: 100vh;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.information {
    width: 100;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: -150px 0 0 0;
}

.information h1 {
    color: var(--c4);
    font-size: 4em;
    margin-bottom: 0;
}

.information h3 {
    color: var(--c2);
    font-size: 1.3em;
    margin-bottom: 0;
}

.minha {
    width: 13em;
    height: 14em;
    border-radius: 50%;
    box-shadow: 1px 1px 10px var(--c4);
    margin: -166px 0 26px 0;
}

.main {
    display: inline-flex;
}

.list {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin: 65px 0 0 0;
    margin-bottom: 0 !important;
    font-family: 'Noto Sans', sans-serif;
}

.list li {
    margin: 0 35px 0 0;
}

.list li a {
    font-size: 1.1em;
    color: var(--c2);
    transition: 0.3s;
}

.list li a:hover {
    border-bottom: 4px solid var(--c4);
    transition: 0.3s;
}

@media(max-width: 800px) {
    .information h1 {
        font-size: 1.5em;
    }
    .information h3 {
        font-size: 0.95em;
    }
    .menu {
        width: 100vw;
        height: 70px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: fixed;
        margin-top: -45%;
    }
}