@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;500;600&display=swap');
:root {
    --c1: #363636;
    --c2: #ffffff;
    --c3: #FF6347;
    --c4: #FF8C00;
    --c5: #FF7F50;
}

ul,
a {
    text-decoration: none;
    list-style: none;
}

.App {
    display: flex;
    width: 100vw;
    height: 100%;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
    background-color: var(--c1);
    color: var(--c2);
}

.section-init {
    display: flex;
    width: 100vw;
    height: 100vh;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.menu {
    width: 100vw;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    margin-top: -45%;
}

.menu img {
    margin: 65px;
    margin-bottom: 0 !important;
}

.menu img {
    width: 50px;
    height: 50px;
    color: var(--c2);
}

.button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 7em;
    height: 3em;
    background-image: linear-gradient( -0.2turn, var(--c3), var(--c5), var(--c4));
    font-size: 1em;
    border-radius: 35px;
    color: var(--c2);
    font-weight: 500;
    box-shadow: 1px 1px 15px var(--c2);
    transition: 0.6s;
    margin: 65px;
    margin-bottom: 0 !important;
}

.menu a:hover {
    transform: scale(1.1);
    transition: 0.6s;
    box-shadow: none;
}

.information {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.information h1 {
    color: var(--c4);
    font-size: 4em;
}

.information h3 {
    color: var(--c2);
    font-size: 1.3em;
}

.minha {
    width: 10em;
    height: 15em;
    border-radius: 50%;
    box-shadow: 1px 1px 10px var(--c4);
    margin: -26px 0 26px 0;
}

.main {
    display: inline-flex;
}

.list {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin: 65px 0 0 0;
    margin-bottom: 0 !important;
    font-family: 'Noto Sans', sans-serif;
}

.list li {
    margin: 0 35px 0 0;
}

.list li a {
    font-size: 1.1em;
    color: var(--c2);
    transition: 0.3s;
}

.list li a:hover {
    border-bottom: 4px solid var(--c4);
    transition: 0.3s;
}

@media(max-width: 800px) {
    .menu {
        background-color: var(--c1);
        margin-top: -600px !important;
        justify-content: space-between;
    }
    .button {
        width: 5em;
        margin-left: -15px;
        margin-right: 10px;
    }
    .list {
        margin-left: 45px !important;
    }
    .logos {
        width: 30px;
        height: 30px;
        margin-top: -80px;
        margin-left: -50px;
    }
}