@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;500;600&display=swap');
:root {
    --c1: #363636;
    --c2: #ffffff;
    --c3: #FF6347;
    --c4: #FF8C00;
    --c5: #FF7F50;
}

* {
    margin: 0;
    padding: 0;
}

.contato {
    width: 100%;
    height: max-content;
    background-color: var(--c1);
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.contato .informacoes {
    width: 300px;
    height: max-content;
    font-family: 'Noto Sans', sans-serif;
}

.informacoes div {
    margin-bottom: 40px;
}

.informacoes a {
    display: flex;
    color: var(--c2);
    margin-left: 100px;
}

.informacoes a i {
    margin-right: 10px;
    font-size: 1.4em;
    line-height: 50%;
}

.contato .form-contato {
    width: 100%;
    height: max-content;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 0;
}

.form-contato h1 {
    font-weight: 200;
    font-size: 2em;
    margin: 100px 0 0 0;
}

.inputs {
    width: 350px;
    height: 30px;
    background-color: transparent;
    border: none;
    border-bottom: 2px solid var(--c2);
    margin: 15px;
    font-family: 'Noto Sans', sans-serif;
    font-size: 1.1em;
    color: var(--c2);
    outline: none;
    transition: 0.3s;
}

.inputs::placeholder {
    padding: 3px;
}

.inputs:focus {
    border-bottom: 3px solid var(--c4);
    transition: 0.4s ease-in;
}

.inputs-textarea {
    width: 350px;
    height: 70px;
    background-color: transparent;
    border: none;
    border-bottom: 2px solid var(--c2);
    margin: 15px;
    font-family: 'Noto Sans', sans-serif;
    outline: none;
    font-size: 1.1em;
    color: var(--c2);
    transition: 0.3s;
}

.inputs-textarea::placeholder {
    padding: 3px;
}

.inputs-textarea:focus {
    border-bottom: 3px solid var(--c4);
    transition: 0.4s ease-in;
}

.button-submit {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 7em;
    height: 3em;
    background-image: linear-gradient( -0.2turn, var(--c4), var(--c3), var(--c5));
    font-size: 1em;
    border-radius: 35px;
    color: var(--c2);
    font-weight: 500;
    box-shadow: 1px 1px 15px var(--c2);
    transition: 0.6s;
    border: none;
    cursor: pointer;
    font-family: 'Noto Sans', sans-serif;
    font-weight: 600;
    font-size: 1.1em;
    outline: none !important;
    margin: 40px 0 40px 0;
    transition: 0.3s;
}

.button-submit:hover {
    transform: scale(1.1);
    transition: 0.3s;
}

@media(max-width: 800px) {
    .contato {
        flex-direction: column;
        justify-content: center;
    }
    .informacoes {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin-top: 500px;
        margin-left: -90px;
    }
}